.fontFace('@{font-name}', '@{font-family-regular}', 400);
.fontFace('@{font-name}', '@{font-family-bold}', 700);
.fontFace('@{font-name}', '@{font-family-black}', 900);

.fontFace('@{font-body-name}', '@{font-body-family-light}', 300);
.fontFace('@{font-body-name}', '@{font-body-family-regular}', 400);
.fontFace('@{font-body-name}', '@{font-body-family-medium}', 500);
.fontFace('@{font-body-name}', '@{font-body-family-bold}', 700);
.fontFace('@{font-body-name}', '@{font-body-family-black}', 900);

p {
    font-size: @font-size-normal;
    
}
h1, h2, h3{
    font-weight: 900;
    span {
        color:@brand-primary;
    }
}
.btn-lg{
    font-size: @font-size-normal;
}

.jumbotron {
    h1 {font-weight: 700;}
    &.primary h1{
        font-weight: 900;
    }
}

.section {
    
    h1, h2 {
        text-transform: uppercase;
        margin-bottom: 40px;
    }

    .thumbnail {

        p {
            font-size: @font-size-small;
        }
    }
    &.inverse{        
         p { font-weight: 400; }
         h1, h2, h3{
            span {
                color:#fff;
            }
        }
    }
    &.section-button {
        p {
            font-family: @font-name, @font-fallback;
            font-size: @font-size-medium;
        }
    }
    &.section-primary, &.section-list {
        p{
            font-weight: 500;
        }        
        h1 {
            font-size: @font-size-xxlarge;
            font-weight: 900;
            margin-bottom: 10px;
        }
        .btn {margin-top: 10px}
    }
    &.section-list,
    &.section-primary.list {
        p, ul, ol, a {
            font-size: @font-size-small;
            font-weight: 500;
        }
        h1{
            margin-bottom:15px;
        }
        h3{
            font-size: @font-size-small;
            font-weight: 900;
            text-transform: uppercase;
        }
        ul{        
            .list-unstyled;
            li{
                &:before{
                    content:"\2022 ";
                    padding-right: 5px;
                }
            }
        }
        ol{
            list-style-position: inside;
            padding: 0;
        }
        .odd {
            h1 {
                font-size: @font-size-xlarge;
                font-weight: 900;
            }
        }        
    }
    &.section-thumbnails{
        .thumbnail{
            .caption{
                h1, h2, h3 {
                    font-weight: 700;
                }
            }
        }
    }
    &.section-blog {
        .thumbnail {
            .caption{
                height: @blog-thumb-height;
                h1{
                    font-weight: 900;
                    font-size: @font-size-medium;
                    text-transform: uppercase;
                    margin-bottom: 18px;
                }
                p{
                    font-size: @font-size-normal;
                }
                a {
                    font-weight: 700;
                    font-family: @font-name, @font-fallback;
                    font-size: @font-size-small;
                    text-decoration: underline;
                }
            }
        }
    }
}

@media (max-width: @screen-sm) {
    .jumbotron h1 {
        font-size: 2.8em;
    }
    p, .thumbnail p{
        font-size: 1em;
    }
    .section.section-button p,
    .jumbotron p,{
        font-size: 1.2em;
    }

    .section.section-list h1,
    .section.section-primary h1 {
        font-size: 1.8em;
    }
    
}