}

.btn {
    font-weight: bold;
    text-transform: uppercase;
}
.btn-lg {
    padding-left: 30px;
    padding-right: 30px;
}

.navbar-default{
    border:none;
    .navbar-header{
        background-color: #fff;
    }
    
    @media (min-width: @grid-float-breakpoint) {
        &{
            background:#fff;
        }
    }

    @media (max-width: @grid-float-breakpoint-max) {
        .navbar-nav {
            margin-top:0;
            margin-bottom:0;
            li {
                border-top:1px solid transparent;
                border-bottom:1px solid transparent;
                a {
                    text-align: center;
                    background:@gray-base;
                    color: #fff;
                    &.btn{display: none;}
                }
            }
            &>.active>a,
            &>.active>a:focus, 
            &>.active>a:hover, 
            &>li>a:focus, 
            &>li>a:hover {
                background-color: @gray-base;
                color:@brand-primary;
            }
        }
    }
    .navbar-toggle {
        margin-top: 13px;
        margin-bottom: 13px;
        .icon-bar{
            width: 30px;
            height: 4px;
            background-color: @brand-primary;
            .borderRadius(2px);
            &+.icon-bar{
                margin-top:6px;
                background-color: @gray-base;
            }
        }
        &:hover, 
        &:focus, 
        &.collapsed {background-color: transparent;}
    }
}

.navbar-nav>li{
    >a{
    @media (min-width: @grid-float-breakpoint){
        .transition(box-shadow);
        .box-shadow(~"inset 0 0 0 0 @{brand-primary}");
    }
    &.btn{
            .box-shadow(none);
            margin-top: 10px;
            padding: 13px 26px;
            display: inline-block;
        }
        &.btn-primary{
            color:@btn-primary-color;
        }
    
   }
   &:hover>a,
   &>a:hover,
   &>a:focus {
       @media (min-width: @grid-float-breakpoint){
        .box-shadow(~"inset 0 3px 0 0 @{brand-primary}");
         &.btn{
            .box-shadow(none);
        }
     }
   }
}

.navbar-brand{
    padding-top: @logo-top;
    img {max-width: @logo-width;}
}

.navbar + .jumbotron {
    margin-top:-20px;
}

.jumbotron {
    color:#fff;
    background:darken(@brand-primary, 40%);
    h1 {text-transform: uppercase;}
    position: relative;
    margin-bottom: 0;
    
    .after {
        .absCenter();
        z-index: 1;
    }
    .container{
        position: relative;
        z-index: 2;
    }
    &.primary{    
        @media (min-width: @screen-sm) {
            min-height:@jumbotron-primary-min-height;
        }
        @media (max-width: calc(@screen-sm - 1)) {
                min-height: @mobile-min-height;
        }
        padding: 12% 0;    
        background-image:url('@{images-path}bg-home.jpg');
        .bgFixed();        
        .after{
            background-color:@gray-base;
            opacity: .4;
        }
        .container>.column-width {max-width: @jumbotron-line-length;}        
        &.long{
            padding-top: @section-padding;  
            padding-bottom: @section-padding;  
            .container>.column-width {
                max-width: @jumbotron-line-length-long;
                p{max-width: @jumbotron-line-length;}
            }
        }
    }
    &.default{
        @media (min-width: @screen-sm) {
            min-height:@jumbotron-default-min-height;
        }
        @media (max-width: calc(@screen-sm - 1)) {
                min-height: @mobile-min-height/2;
        }
         background-image:url('@{images-path}bg-contact.jpg');
        .bgFixed();
        .after{
            background-color:@brand-primary;
            opacity: .5;
        }
        
        text-align: center;
    }
    @media (max-width: @screen-sm) {
        display: table;
        width: 100%;
        padding-top:@section-padding-xs;
        padding-bottom:@section-padding-xs;
        &.primary.long{
            padding-top:@section-padding-xs;
            padding-bottom:@section-padding-xs;
        }
        .container{
            display: table-cell;
            vertical-align: middle;
        }
    }
    &.top{
        background-position: center top;
    }
    &.center{
        background-position: center center;
    }
    &.bottom {
        background-position: center bottom;
    }
}

